.container {
    display: flex;
    justify-content: space-between;
    column-gap: 20px;
}

@keyframes transitionFade {
    0%{
        opacity: 0.1;
    }
    100% {
        opacity: 1;
    }
}

.main {
    width: 60%;
    padding: 2rem 0;
    display: flex;
    flex-direction: column;
    row-gap: 15px;
    animation: transitionFade .3s;
}

.main .banner {
    width: 100%;
    background-color: var(--primary-color);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 20px;
    box-sizing: border-box;
    border-radius: 10px;
}

.main .banner img {
    width: 50%;
}

.main .banner h2 {
    font-size: var(--body-size);
    color: white;
}

.main .subject {
    margin-top: 1.5rem;
}

.side {
    width: 40%;
    height: 500px;
    padding: 2rem 0;
}

@media (max-width: 600px) {
    .side {
        display: none;
    }

    .container {
        flex-direction: column;
    }
    
    .main {
        width: 100%;
    }
    
}